import { Tree } from "antd"
import * as d3 from "d3"
import { findSimiliarColor } from "../utils/color"
import { Loader } from "./Loader"
import { Icon, processIcon } from "../components/Icon"

function DescriptionTerm({ term, visibleElements }) {
    const styleParameter = (word) => {
        if (Array.isArray(word)) {
            return word.map(
                (_) => `<span class='description-parameter'>${_}</span>`
            )
        }

        // 
        if (/^#(?:[0-9a-fA-F]{3}){1,2}$/.test(word)) {
            const name = findSimiliarColor(word)
            return `<span class='description-parameter'>${name}(<span style='color: ${word};'>&#9724;</span>${word})</span>`
        }

        return `<span class='description-parameter'>${word}</span>`
    }
    return (
        <p
            style={{
                margin: 0,
                padding: 2,
                fontFamily: "Times",
            }}
            onMouseOver={(e) => {
                e.target.style.backgroundColor = "#e9e9e9"
                visibleElements.forEach((e) => {
                    d3.select(e).attr("opacity", 0.1)
                })
                term.elements.forEach((e) => {
                    d3.select(e).attr("opacity", 1)
                })
            }}
            onMouseLeave={(e) => {
                e.target.style.backgroundColor = "#fff"
                visibleElements.forEach((e) => {
                    d3.select(e).attr("opacity", 1)
                })
            }}
            dangerouslySetInnerHTML={{
                __html: term.content,
                // __html: term.template(
                //     ...term.parameters.map(
                //         styleParameter
                //     )
                // ),
            }}
        ></p>
    )
}

function getTreeData(
    graphDescriptions,
    visibleElements,
    defaultExpandNodes,
    parentKey = ""
) {
    const res = []
    graphDescriptions.forEach((term, i) => {
        const key = `${parentKey}-${i}`
        if (term.switch) {
            defaultExpandNodes.push(key)
        }
        res.push({
            title: (
                <DescriptionTerm
                    visibleElements={visibleElements}
                    term={term}
                />
            ),
            key: key,
            children:
                Array.isArray(term.children) && term.children.length > 0
                    ? getTreeData(
                          term.children,
                          visibleElements,
                          defaultExpandNodes,
                          key
                      )
                    : null,
        })
    })
    return res
}

function TreeDescription({ graphDescriptions, visibleElements }) {
    let treeData = []
    const defaultExpandNodes = []
    if (graphDescriptions && graphDescriptions.length) {
        treeData = getTreeData(
            graphDescriptions,
            visibleElements,
            defaultExpandNodes
        )
    }
    return (
        <Tree
            selectable={false}
            treeData={treeData}
            defaultExpandNodes={defaultExpandNodes}
        />
    )
}

function isFloat(n) {
    return Number(n) == n && n % 1 !== 0
}

function isNumber(n) {
    return Number(n) == n
}

function processFloat(x) {
    if (isFloat(x)) {
        return Number(x).toFixed(2)
    }
    return x
}

function upperFirst(s) {
    return s.charAt(0).toUpperCase() + s.slice(1)
}

function DescriptionLegendTerm({ term }) {
    return (
        <p
            style={{
                margin: 0,
                padding: 2,
                fontFamily: "Times",
            }}
            dangerouslySetInnerHTML={{
                __html: term.content,
            }}
        ></p>
    )
}

function getTreeDataLegend(
    descriptions,
    data,
    elementInfo,
    defaultExpandNodes,
    entityElementsGroups,
    visibleElements,
    parentKey = "",
    SVG
) {
    let res = []

    descriptions.forEach((term, i) => {
        const key = `${parentKey}-${i}`
        if (term.switch) {
            defaultExpandNodes.push(key)
        }

        const parameters = {}
        term.parameters.forEach((p) => {
            parameters[p.type] = p.value
        })

        // 
        if (
            "channel-parameter" in parameters &&
            "attribute-parameter" in parameters
        ) {
            const children = []
            const attr = parameters["attribute-parameter"]
            const duplicate = new Map()
            const maxBBox = d3.max(term.elements, (e) =>
                Math.max(e.getBBox().width, e.getBBox().height)
            )
            for (let i = 0; i < term.elements.length; i++) {
                // NOTE: 
                const dataTerm =
                    data[elementInfo[term.elementsIdx[i]].entityType + "s"][
                        elementInfo[term.elementsIdx[i]].entityIndex
                    ]
                const attrValue = processFloat(
                    attr.indexOf("@") !== -1
                        ? dataTerm[attr.split("@")[0]][attr.split("@")[1]]
                        : dataTerm[attr]
                )

                let channel = term.elements[i].getAttribute(
                    parameters["channel-parameter"]
                )
                if (
                    parameters["channel-parameter"].indexOf("textContent") !==
                    -1
                ) {
                    channel = term.elements[i].innerHTML
                }
                let channelElement = (
                    <span className="description-parameter attribute-parameter">
                        {processFloat(channel)}
                    </span>
                )

                if (/^#(?:[0-9a-fA-F]{3}){1,2}$/.test(channel)) {
                    const name = findSimiliarColor(channel)
                    channelElement = (
                        <span class="description-parameter">
                            {name}(
                            <span style={{ color: channel }}>&#9724;</span>
                            {channel})
                        </span>
                    )
                }
                const key = `${channel}${attrValue}`
                const isDuplicate = duplicate.has(key)
                if (isDuplicate) {
                    duplicate.get(key).push(term.elements[i])
                    continue
                } else {
                    duplicate.set(key, [term.elements[i]])
                }

                // TODO: icon, bbox
                const elIcon = processIcon(
                    {
                        group: term.elements[i],
                        bbox: term.elements[i].getBBox(),
                    },
                    30,
                    maxBBox
                )
                const orIcon = term.elements[i]

                const title = (
                    <div
                        style={{
                            paddingRight: "10px",
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style.backgroundColor = "#e9e9e9"
                            visibleElements.forEach((e) => {
                                d3.select(e).attr("opacity", 0.1)
                            })
                            duplicate.get(key).forEach((e) => {
                                d3.select(e).attr("opacity", 1)
                            })
                        }}
                        onMouseLeave={(e) => {
                            e.currentTarget.style.backgroundColor = "#fff"
                            visibleElements.forEach((e) => {
                                d3.select(e).attr("opacity", 1)
                            })
                        }}
                    >
                        {elIcon && <Icon el={elIcon} width={30} />}
                        <span className="description-parameter channel-parameter">
                            {parameters["channel-parameter"]}
                        </span>
                        <span>: </span>
                        {channelElement}
                        <span>  </span>
                        <span className="description-parameter attribute-parameter">
                            {attr.indexOf("@") !== -1
                                ? `${attr.split("@")[0]}[${attr.split("@")[1]}]`
                                : attr}
                        </span>
                        <span>: </span>
                        <span className="description-parameter value-parameter">
                            {attrValue}
                        </span>
                    </div>
                )

                children.push({
                    orIcon,
                    elIcon,
                    channel,
                    attrValue,
                    title: title,
                    key: `${key}-${i}`,
                })
            }

            children.sort((e1, e2) => {
                function compare(a, b) {
                    let channel1 = a
                    let channel2 = b
                    if (isNumber(channel1) && isNumber(channel2)) {
                        channel1 = Number(channel1)
                        channel2 = Number(channel2)
                    }
                    if (channel1 > channel2) return 1
                    else if (channel1 < channel2) return -1
                    else return 0
                }

                if (e1.channel === e2.channel) {
                    return compare(e1.attrValue, e2.attrValue)
                } else {
                    return compare(e1.channel, e2.channel)
                }
            })

            const elIcons = [
                children[children.length - 1].orIcon.cloneNode(true),
                children[children.length - 1].orIcon.cloneNode(true),
                children[children.length - 1].orIcon.cloneNode(true),
            ]
            if (parameters["channel-parameter"][0] === "textContent") {
                elIcons[0].textContent = children[0].channel
                elIcons[1].textContent =
                    children[Math.floor(children.length / 2)].channel
            } else {
                elIcons[0].setAttribute(
                    parameters["channel-parameter"][0],
                    children[0].channel
                )
                elIcons[1].setAttribute(
                    parameters["channel-parameter"][0],
                    children[Math.floor(children.length / 2)].channel
                )
            }
            const clonedSVG = SVG.cloneNode()
            document.body.appendChild(clonedSVG)
            const elIconsBBox = []
            const elIconsMaxBBox = elIcons.reduce((maxBBox, el) => {
                clonedSVG.appendChild(el)
                const bbox = el.getBBox()
                elIconsBBox.push(bbox)
                return Math.max(Math.max(bbox.width, bbox.height), maxBBox)
            }, 0)
            document.body.removeChild(clonedSVG)
            elIcons.forEach((el) => el.remove())
            clonedSVG.remove()
            // if (parameters["channel-parameter"][0] === "y1") debugger
            const title = (
                <div
                    style={{
                        padding: 5,
                    }}
                    onMouseEnter={(e) => {
                        e.currentTarget.style.backgroundColor = "#e9e9e9"
                        visibleElements.forEach((e) => {
                            d3.select(e).attr("opacity", 0.1)
                        })
                        term.elements.forEach((e) => {
                            d3.select(e).attr("opacity", 1)
                        })
                    }}
                    onMouseLeave={(e) => {
                        e.currentTarget.style.backgroundColor = "#fff"
                        visibleElements.forEach((e) => {
                            d3.select(e).attr("opacity", 1)
                        })
                    }}
                >
                    {elIcons &&
                        elIcons.map((elIcon, i) => {
                            const bbox = elIconsBBox[i]
                            const k = (30 * 0.8) / elIconsMaxBBox
                            const newElement = elIcon.cloneNode(true)
                            d3.select(newElement).attr(
                                "transform",
                                `scale(${k})translate(${-bbox.x + 2}, ${
                                    -bbox.y + 2
                                })`
                            )
                            return (
                                <span style={{ marginRight: 3 }}>
                                    <Icon
                                        el={newElement}
                                        viewBox={`0 0 30 30`}
                                        key={i}
                                        width={30}
                                    />
                                </span>
                            )
                        })}
                    <span className="description-parameter channel-parameter">
                        {parameters["channel-parameter"]}
                    </span>
                    <span>  </span>
                    <span className="description-parameter attribute-parameter">
                        {attr.indexOf("@") !== -1
                            ? `${attr.split("@")[0]}[${attr.split("@")[1]}]`
                            : attr}
                    </span>
                </div>
            )
            res.push({
                title: title,
                key: key,
                children,
            })
        } else if ("entity-parameter" in parameters) {
            // Node/Link
            res.push({
                title: (
                    <div className="description-parameter entity-parameter">
                        {upperFirst(parameters["entity-parameter"])}
                    </div>
                ),
                key: key,
                children:
                    Array.isArray(term.children) && term.children.length > 0
                        ? getTreeDataLegend(
                              term.children,
                              data,
                              elementInfo,
                              defaultExpandNodes,
                              entityElementsGroups,
                              visibleElements,
                              key,
                              SVG
                          )
                        : null,
            })
        } else {
            if (
                Array.isArray(parameters["element-parameter"]) &&
                parameters["element-parameter"].length > 1
            ) {
                // NOTE: tagName
                console.log(parameters)
                const tagAttr = term.children[0].parameters.filter(
                    (x) => x.type === "attribute-parameter"
                )[0].value
                const title = (
                    <span
                        style={{
                            padding: 5,
                        }}
                        onMouseEnter={(e) => {
                            e.currentTarget.style.backgroundColor = "#e9e9e9"
                            visibleElements.forEach((e) => {
                                d3.select(e).attr("opacity", 0.1)
                            })
                            term.elements.forEach((e) => {
                                d3.select(e).attr("opacity", 1)
                            })
                        }}
                        onMouseLeave={(e) => {
                            e.currentTarget.style.backgroundColor = "#fff"
                            visibleElements.forEach((e) => {
                                d3.select(e).attr("opacity", 1)
                            })
                        }}
                    >
                        <span className="description-parameter channel-parameter">
                            tagName
                        </span>
                        <span>  </span>
                        <span className="description-parameter attribute-parameter">
                            {tagAttr}
                        </span>
                    </span>
                )
                let children = []

                term.children.forEach((child, childIdx) => {
                    const childParameters = {}
                    child.parameters.forEach((p) => {
                        childParameters[p.type] = p.value
                    })
                    const attr = childParameters["attribute-parameter"]
                    const attrValue = childParameters["value-parameter"]
                    const duplicate = new Map()
                    // TODO: icon, bbox
                    for (let i = 0; i < child.elements.length; i++) {
                        const elIcon = processIcon(
                            {
                                group: child.elements[i],
                                bbox: child.elements[i].getBBox(),
                            },
                            30,
                            30
                        )

                        const key = `${childParameters["element-parameter"]}${attrValue}`
                        const isDuplicate = duplicate.has(key)
                        if (isDuplicate) {
                            duplicate.get(key).push(child.elements[i])
                            continue
                        } else {
                            duplicate.set(key, [child.elements[i]])
                        }

                        const title = (
                            <div
                                style={{
                                    paddingRight: "10px",
                                }}
                                onMouseEnter={(e) => {
                                    e.currentTarget.style.backgroundColor =
                                        "#e9e9e9"
                                    visibleElements.forEach((e) => {
                                        d3.select(e).attr("opacity", 0.1)
                                    })
                                    duplicate.get(key).forEach((e) => {
                                        d3.select(e).attr("opacity", 1)
                                    })
                                }}
                                onMouseLeave={(e) => {
                                    e.currentTarget.style.backgroundColor =
                                        "#fff"
                                    visibleElements.forEach((e) => {
                                        d3.select(e).attr("opacity", 1)
                                    })
                                }}
                            >
                                {elIcon && <Icon el={elIcon} width={30} />}
                                <span className="description-parameter channel-parameter">
                                    {"tagName"}
                                </span>
                                <span>: </span>
                                <span
                                    class="description-parameter"
                                    dangerouslySetInnerHTML={{
                                        __html: childParameters[
                                            "element-parameter"
                                        ],
                                    }}
                                ></span>
                                <span>  </span>
                                <span className="description-parameter attribute-parameter">
                                    {attr.indexOf("@") !== -1
                                        ? `${attr.split("@")[0]}[${
                                              attr.split("@")[1]
                                          }]`
                                        : attr}
                                </span>
                                <span>: </span>
                                <span className="description-parameter value-parameter">
                                    {attrValue}
                                </span>
                            </div>
                        )

                        children.push({
                            title: title,
                            key: `${key}--${childIdx}--${i}`,
                        })
                    }
                })

                res.push({
                    title: title,
                    key: key,
                    children,
                })
            }

            // children
            res = res.concat(
                Array.isArray(term.children) && term.children.length > 0
                    ? getTreeDataLegend(
                          term.children,
                          data,
                          elementInfo,
                          defaultExpandNodes,
                          entityElementsGroups,
                          visibleElements,
                          key,
                          SVG
                      )
                    : null
            )
        }
    })

    return res
}

export function DescriptionsLegend({
    data,
    elementInfo,
    entityElementsGroups,
    graphDescriptions,
    visibleElements,
    loadingText,
    loading,
    SVG,
}) {
    if (
        !elementInfo ||
        !data ||
        !graphDescriptions ||
        !entityElementsGroups ||
        !("node" in entityElementsGroups)
    )
        return null

    console.log(graphDescriptions)
    console.log(data)
    console.log(elementInfo)

    let treeData = []
    const defaultExpandNodes = []
    if (!loading && graphDescriptions && graphDescriptions.length) {
        treeData = getTreeDataLegend(
            graphDescriptions,
            data,
            elementInfo,
            defaultExpandNodes,
            entityElementsGroups,
            visibleElements,
            "",
            SVG
        )
        console.log(treeData)
    }

    return (
        <div
            style={{
                margin: 20,
                marginLeft: 30,
            }}
        >
            {!loading && graphDescriptions && graphDescriptions.length ? (
                <Tree
                    style={{
                        fontSize: 18,
                    }}
                    selectable={false}
                    treeData={treeData}
                    defaultExpandedKeys={defaultExpandNodes}
                />
            ) : (
                // graphDescriptions.map((term, i) => {
                //     return <DescriptionTerm
                //         visibleElements={visibleElements}
                //         term={term}
                //     />
                // })
                <Loader
                    style={{
                        margin: 20,
                        marginLeft: 28,
                        padding: 2,
                        fontFamily: "Times",
                    }}
                    state={loadingText}
                ></Loader>
            )}
        </div>
    )
}
