import React from "react"
import * as d3 from "d3"

function processIcon(element, width, maxBBox) {
    const bbox = element.bbox
    const k = width / maxBBox
    const newElement = element.group.cloneNode(true)
    d3.select(newElement).attr(
        "transform",
        `scale(${k})translate(${-(bbox.x + bbox.width / 2)}, ${-(
            bbox.y +
            bbox.height / 2
        )})`
    )

    return newElement
}

function Icon({ el, width, viewBox }) {
    if (!el) return null

    return (
        <svg
            width={width}
            height={width}
            style={
                el.tagName === "line"
                    ? {
                          verticalAlign: "middle",
                          stroke: "#999",
                          opacity: "0.6",
                      }
                    : {
                          verticalAlign: "middle",
                      }
            }
            viewBox={
                viewBox
                    ? viewBox
                    : `-${width / 2} -${width / 2} ${width} ${width}`
            }
            dangerouslySetInnerHTML={{
                __html: new XMLSerializer().serializeToString(el),
            }}
        ></svg>
    )
}

export { processIcon, Icon }
